<template>
	<div id="more">
		<public-header title="更多"></public-header>
		<public-footer :menufooter="menufooter"></public-footer>
		<div class="content">
			<div class="group">
				<van-row gutter="20">
					<van-col span="8" v-if="util.checkperm(item.checkperm)" v-for="(item, index) in urls.system" :key="item.id">
						<router-link tag="div" class="deliveryer-item" :to="util.getUrl({path: item.url})">
							<div class="top">
								<i :class="'icon' + ' '+ item.icon + ' ' + item.iconColor"></i>
							</div>
							<div class="bottom">
								<span>{{item.title}}</span>
							</div>
						</router-link>
					</van-col>
				</van-row>
			</div>
			<div class="group">
				<van-row gutter="20">
					<van-col span="8" v-if="util.checkperm(item.checkperm)" v-for="(item, index) in urls.plugin" :key="item.id">
						<router-link tag="div" class="deliveryer-item" :to="util.getUrl({path: item.url})">
							<div class="top">
								<i :class="'icon' + ' '+ item.icon + ' ' + item.iconColor"></i>
							</div>
							<div class="bottom">
								<span>{{item.title}}</span>
							</div>
						</router-link>
					</van-col>
				</van-row>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import publicFooter from "@/components/footer"
	import {getData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader,
			publicFooter
		},
		data() {
			return {
				menufooter: this.util.getStorage('menufooter'),
				urls: {
					system: [],
					plugin: []
				},
				showPreLoading: true
			}
		},
		methods: {
			onLoad(){
				var that = this;
				getData({
					vue: this,
					url: 'plateform/more/index/index',
					success: (res) => {
						that.urls.system = res.urls.system;
						that.urls.plugin = [...that.urls.plugin, ...res.urls.plugin];
					}
				});
			},
		},
		mounted(){
			this.onLoad();
			this.util.imap();
			this.util.icloudapi();
		}
	}
</script>

<style>
	#more .content{
		background-color: #fff;
	}
	#more .content .group{
		padding: 20px 15px;
	}
	#more .content .group:last-child{
		padding-top: 0px;
	}
	#more .content .group .van-row{
		text-align: center;
	}
	#more .content .group .deliveryer-item{
		margin-bottom: 20px;
	}
	#more .content .group .deliveryer-item .top{
		padding: 10px;
	}
	#more .content .group .deliveryer-item .top .icon{
		font-size: 40px;
	}
	#more .content .group .deliveryer-item .bottom{
		font-size: 14px;
		color: #7D8484;
	}
	#more .content .group  .c-yellow{
		color: #FFD700;
	}
</style>


